import App from './App.vue'
import DirexxtPrivacyPolicy from './components/DirexxtPrivacyPolicy.vue';
import VueRouter from 'vue-router'

const routes = [
    { path: '*', component: App },
    { path: '/privacy-policy/direxxt', component: DirexxtPrivacyPolicy },
];

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
});

export default router
